    }

    .dice_roll_desc {
        position: absolute;
        color: white;
        top: 75%;
        right: 30%;
        font-size: 3vw;
        width: 40%;
        filter: drop-shadow(0px 0px 15px #292929);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .dice_image {
        position: absolute;
        color: white;
        top: -20%;
        right: 20%;
        font-size: 1vw;
        width: 60%;
        filter: drop-shadow(0px 0px 15px #292929);
        cursor: pointer;
    }

    .dice_image[wobble="1"] {
        animation-name: wobble;
        animation-duration: 2s;
    }


    /* Setting Animation effect to the 
     dice using css keyframe */
    @keyframes wobble {
        from {
            transform: translate3d(0, 0, 0);
        }

        15% {
            transform: translate3d(-25%, 0, 0) rotate3d(0, 0, 1, -5deg);
        }

        30% {
            transform: translate3d(20%, 0, 0) rotate3d(0, 0, 1, 3deg);
        }

        45% {
            transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -3deg);
        }

        60% {
            transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 2deg);
        }

        75% {
            transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1deg);
        }

        to {
            transform: translate3d(0, 0, 0);
        }
    }

    .cards_and_button_container {
        position: absolute;
        color: white;
        top: 40%;
        right: 15%;
        filter: drop-shadow(0px 0px 15px #292929);
        width: 70%;

    }

    .check_button {
        display: inline-block;
        padding: 15px 25px;
        font-size: 1vw;
        cursor: pointer;
        text-align: center;
        text-decoration: none;
        outline: none;
        color: #000000;
        background-color: #ffffff;
        border: none;
        border-radius: 15px;
        box-shadow: 0 9px #999;
        margin: 5%;
    }

    .check_button:active {
        background-color: #979797;
        box-shadow: 0 5px #666;
        transform: translateY(4px);
    }

    .RPS_before_game_container {}

    .RPS_before_game_container_gone {
        display: none;
        visibility: hidden;
    }

    .RPS_before_click {
        position: absolute;
        color: rgb(255, 255, 255);
        background-color: #29649C;
        border-radius: 50px;
        top: 15%;
        right: 25%;
        font-size: 8vw;
        width: 50%;
        padding-top: 5%;
        padding-bottom: 5%;
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        cursor: pointer;
        filter: drop-shadow(0px 0px 15px #292929);

    }

    .RPS_before_description {
        position: absolute;
        color: rgb(0, 0, 0);
        top: 70%;
        right: 24%;
        font-size: 4vw;
        width: 50%;
