        text-align: justify;
        animation: fadeIn 0.5s;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .RPS_countdown {
        position: absolute;
        color: rgb(0, 0, 0);
        top: 5%;
        right: 25%;
        font-size: 12vw;
        width: 50%;
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .rps_image {
        position: absolute;
        top: 25%;
        left: 25%;
        width: 50%;
    }

    .fiftytwocards {
        position: absolute;
        right: 38%;
        width: 22%;
        margin-top: -75%;
        cursor: pointer;
        filter: drop-shadow(0px 0px 5px #292929);
    }
    .fiftytwocard_back {
        position: absolute;
        right: 38%;
        width: 22%;
        margin-top: -75%;
        cursor: pointer;
        z-index:10000;
    }
    .fiftytwocard_back_gone{
        display:none;
    }

    .rtb_description {
        position: absolute;
        color: white;
        top: 40%;
        right: 24%;
        font-size: 3vw;
        width: 50%;
        filter: drop-shadow(0px 0px 3px #292929);
        transition: all 0.5s;
        animation: fadeIn 0.5s;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }
}

