/* --------------------------------------------------------------------------------------------------------------------------------------- */
/* The CSS Below is for mobile in portrait orientation*/
@media only screen and (max-width: 649px) {
    .next_card_button {
        position: absolute;
        right: 22%;
        padding: 15px 25px;
        font-size: 10vw;
        cursor: pointer;
        text-align: center;
        text-decoration: none;
        outline: none;
        color: #000000;
        z-index: 2000;
        background-color: #ffffff;
        border: 2px solid #000000;
        border-radius: 30px;
        box-shadow: #000000 4px 4px 0 0;
        color: #000000;
        font-weight: 600;
        text-decoration: none;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        margin-top: 125%;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

    }

    

    .next_card_button:hover {
        background-color: #fff;
    }

    .next_card_button:active {
        box-shadow: #000000 2px 2px 0 0;
        transform: translate(2px, 2px);

    }

    .next_card_button_feature {
        position: absolute;
        right: 22%;
        padding: 15px 25px;
        font-size: 10vw;
        cursor: pointer;
        text-align: center;
        text-decoration: none;
        outline: none;
        color: #000000;
        z-index: 2000;
        background-color: #ffffff;
        border: 2px solid #000000;
        border-radius: 30px;
        box-shadow: #000000 4px 4px 0 0;
        color: #000000;
        font-weight: 600;
        text-decoration: none;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        margin-top: 125%;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .next_card_button_feature:hover {
        background-color: #fff;
    }

    .next_card_button_feature:active {
        box-shadow: #000000 2px 2px 0 0;
        transform: translate(2px, 2px);

    }

    .shearer_popup {
        position: absolute;
        top: 25%;
        width: 40%;
        left: -10%;
        transition: 4s;
    }

    .shearer_popup_gone {
        position: absolute;
        top: 25%;
        width: 40%;
        left: -10%;
        transition: 4s;
        transform: translateX(4000px);

    }

    .speech_bubble {
        position: absolute;
        top: 2%;
        left: 15%;
        width: 60%;
        -webkit-transform: scaleX(-1);
        filter: drop-shadow(0px 0px 15px #292929);
        animation-name: fadeIn;
        animation-duration: 2s;
        animation-iteration-count: 1;
        animation-delay: 4s;
        animation-fill-mode: forwards;
        transition: 2s;
        opacity: 0;
    }

    .speech_bubble_gone {
        width: 60%;
        position: absolute;
        top: 10%;
        left: 15%;
        -webkit-transform: scaleX(-1);
        animation-name: fadeOut;
        animation-duration: 0.2s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        filter: drop-shadow(0px 0px 15px #292929);
        transform: translateX(4000px);
        transition: 4s;
    }

    .shearer_text_name {
        position: absolute;
        top: 5%;
        left: 28%;
        font-size: 5vw;
        animation-name: fadeIn;
        animation-duration: 2s;
        animation-iteration-count: 1;
        animation-delay: 4s;
        animation-fill-mode: forwards;
        transition: 2s;
        opacity: 0;
    }

    .shearer_text_name_gone {
        position: absolute;
        top: 5%;
        left: 28%;
        font-size: 5vw;
        animation-name: fadeOut;
        animation-duration: 0.2s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        filter: drop-shadow(0px 0px 15px #292929);
        transform: translateX(4000px);
        transition: 4s;
    }

    .shearer_text {
        position: absolute;
        top: 12%;
        left: 28%;
        font-size: 5vw;
        animation-name: fadeIn;
        animation-duration: 2s;
        animation-iteration-count: 1;
        animation-delay: 4s;
        animation-fill-mode: forwards;
        transition: 2s;
        opacity: 0;
    }

    .shearer_text_gone {
        position: absolute;
        top: 12%;
        left: 28%;
        font-size: 5vw;
        animation-name: fadeOut;
        animation-duration: 0.2s;
        animation-iteration-count: 1;
        animation-fill-mode: forwards;
        filter: drop-shadow(0px 0px 15px #292929);
        transform: translateX(4000px);
        transition: 4s;
    }


    @keyframes fadeIn {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 100;
        }
    }

    @keyframes fadeOut {
        0% {
            opacity: 100;
        }

        100% {
            opacity: 0;
        }
    }

    @keyframes shearerPulse {
        0% {
            width: 15%;
        }

        50% {
            width: 15.5%;
        }

        100% {
            width: 15%;
        }
    }

    .white_wheel_container {
        position: absolute;
        top: -40%;
        right: -113%;
        font-size: 1.5vw;
        z-index: 1;
    }

    .nameDivs {
        width: 5%;
        display: inline-block;
        margin-left: 5%;
        padding: 10px;
        font-size: 1vw;
        text-align: center;
        color: #000000;
        background-color: #ffffff;
        border: none;
        border-radius: 15px;
        box-shadow: 0 9px #999;

    }



    .game_container_earthquake {
        animation: rotating 0.5s linear infinite;
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        overflow: auto;
    }

    .game_container {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        overflow: hidden;
    }

    @keyframes rotating {
        0% {
            transform: rotate(2deg);
        }

        50% {
            transform: rotate(-2deg);
        }

        100% {
            transform: rotate(2deg);
        }
    }

    .earthquake_container {}

    .allcards {
        z-index: -1;
    }

    .earthquake_container_gone {
        display: none;
    }

    .earthquake_image {
        position: absolute;
        z-index: 10000;
        width: 80%;
        top: 35%;
        left: 10%;
        animation: fadeIn 1s;
    }

    .earthquake_title {
        position: absolute;
        top: 30%;
        right: 15%;
        z-index: 100000;
        font-size: 10vw;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        animation: fadeIn 1s;
        background: rgb(0, 0, 0);
        border-radius: 10px;

    }

    .earthquake_description {
        position: absolute;
        top: 70%;
        right: 16%;
        z-index: 100000;
        font-size: 5vw;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        animation: fadeIn 1s;
        background: rgb(0, 0, 0);
        border-radius: 10px;
    }

    .boozenami_container {}

    .waves_image {
        position: absolute;
        z-index: 1000;
        width: 3500%;
        animation: waves_moving 60s infinite;
        margin-top: -5%;
    }

    @keyframes waves_moving {
        0% {
            transform: translateX(-10%);
        }

        50% {
            transform: translateX(-90%);
        }

        100% {
            transform: translateX(0%);
        }
    }

    .boozenami_image {
        position: absolute;
        z-index: 100000;
        top: 25%;
        width: 70%;
        left: 10%;
        filter: drop-shadow(0px 0px 15px #292929);
    }

    .boozenami_title {
        position: absolute;
        top: 10%;
        left: 20%;
        z-index: 100000;
        font-size: 10vw;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        animation: fadeIn 1s;

    }

    .boozenami_description {
        position: absolute;
        top: 65%;
        right: 8%;
        z-index: 100000;
        font-size: 4vw;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        animation: fadeIn 1s;
    }

    .genie_image {
        position: absolute;
        z-index: 100000;
        top: 60%;
        width: 40%;
        left: -10%;
        filter: drop-shadow(0px 0px 15px #292929);
        animation: genieFadeIn 5s;
    }

    .genie_title {
        position: absolute;
        top: 7%;
        left: 20%;
        z-index: 100000;
        font-size: 5vw;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        animation: genieFadeIn 5s;
        background: #111111;
        border-radius: 10px;
    }

    .genie_description {
        position: absolute;
        top: 75%;
        left: 12%;
        z-index: 100000;
        font-size: 2.5vw;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        animation: genieFadeIn 5s;
        background: #111111;
        border-radius: 10px;
    }

    @keyframes genieFadeIn {
        0% {
            opacity: 0;

        }

        20% {
            opacity: 50%;

        }

        40% {
            opacity: 0;

        }

        60% {
            opacity: 50%;

        }

        80% {
            opacity: 0;

        }


        100% {
            opacity: 100;

        }
    }

    .hamburger_icon {
        position: relative;
        display: inline-block;
        padding-right: 4%;
        padding-left: 0.5%;
        padding-top: 0.2%;
        padding-bottom: 0.2%;
        cursor: pointer;
        text-align: center;
        text-decoration: none;
        outline: none;
        color: #000000;
        background-color: #ffffff;
        border: none;
        border-radius: 15px;
        box-shadow: 0 5px #999;
        margin-top: 5%;
        margin-bottom: 3%;
        margin-left: 5.5%;
        z-index: 100000000000;

    }



    .hamburger_icon:active {
        box-shadow: #000000 2px 2px 0 0;
        transform: translate(2px, 2px);
    }


    .bar1,
    .bar2,
    .bar3 {
        width: 10vw;
        height: 1vw;
        background-color: #000000;
        margin: 18%;
        transition: 0.4s;
        border-radius: 80vw;
    }

    .bar1change {
        width: 10vw;
        height: 1vw;
        background-color: #000000;
        margin: 18%;
        transition: 0.4s;
        transform: translate(0, 270%) rotate(-45deg);
        border-radius: 80vw;

    }

    .bar2change {
        width: 10vw;
        height: 1vw;
        transition: 0.4s;
        opacity: 0;

    }

    .bar3change {
        width: 10vw;
        height: 1vw;
        background-color: #000000;
        margin: 18%;
        transition: 0.4s;
        transform: translate(0, -270%) rotate(45deg);
        border-radius: 80vw;
    }

    .navbar_menu_expanded {
        position: absolute;
        top:12%;
        right: 4%;
        width: 80%;
        background-color: #29649c;
        opacity: 100%;
        z-index: 1000000000;
        border-radius: 50px;
        border-width: 5px;
        border-color: #ffffff;
        border-style: solid;
        filter: drop-shadow(0 0 0.75rem #000000);
    }

    .navbar_menu_gone {
        display: none;

    }
 
    .menuText {  
        color: rgb(255, 255, 255);
        text-align: center;
        font-size: 5vw;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-weight: bold;
        background-color: #161616;
        padding-top: 5%;
        padding-bottom: 5%;
        border-radius: 5px;
        width: 90%;
        margin-left:1%;
        cursor: pointer;
    }

    .menuText:hover {
        background-color: #2d2d2d;

    }

    .leave_confirmation_popup {
        position: absolute;
        margin-top:11%;
        right:9%;
        width: 80%;
        height: 30%;
        background-color: #29649c;
        opacity: 100%;
        border-radius: 50px;
        border-width: 5px;
        border-color: #ffffff;
        border-style: solid;
        filter: drop-shadow(0 0 0.75rem #000000);
        z-index:9999999999999999;
        text-align: center;
    }

    .leave_text {
        color: rgb(255, 255, 255);    
        font-size: 5vw;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-weight: bold;
        width: 60%;
        margin-left: 20%;
        cursor: pointer;
        z-index:9999999999999999;
    }

    .leave_button{
        color: rgb(255, 255, 255);
        font-size: 6vw;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-weight: bold;
        background-color: #111111;
        padding-top: 4%;
        padding-bottom: 4%;
        border-radius: 10px;
        width: 30%;
        margin-left:4%;
        margin-right:4%;
        cursor: pointer;
        z-index:9999999999999999;
    }

    .background_opacity {
        background-color:#000000;
        width:1000vw;
        height:1000vw;
        position:absolute;
        z-index:10;
        margin-top:-100%;
        margin-left:-100%;
        opacity:0.3;
    }

    .disable_features_popup {
        position: absolute;
        display:grid;
        grid-template-columns:33% 33% 33%; 
        z-index: 10000000000;
        top: 55%;
        margin-left:5%;
        filter: drop-shadow(0px 0px 15px #292929);
        background: white;
        border-radius: 10px;
        width: 90%;
        background-color: #29649c;
        border-radius: 10px;
        border-width: 4px;
        border-color: #ffffff;
        border-style: solid;
    }

    .game_without_menu_container {
        opacity: 0;
        pointer-events: none;
    }

    .disable_features_popup_gone {
        display: none;
    }

    .feature_popup_image {
        width: 20vw;
        height: 20vw;
        cursor: pointer;

    }

    .feature_checkbox_on {
        background-color: rgb(71, 177, 65);
        margin-bottom:5%;
        margin-top:5%;
        margin-left:5%;
        text-decoration: none;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        border: none;
        padding: 1%;
        width:90%;
        border-radius: 10px;
        border-width: 2px;
        border-color: #ffffff;
        border-style: solid;
    }

    .feature_checkbox_off {
        background-color: rgb(172, 56, 56);
        margin-bottom:5%;
        margin-top:5%;
        margin-left:5%;
        text-decoration: none;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        border: none;
        padding: 1%;
        width:90%;
        border-radius: 10px;
        border-width: 2px;
        border-color: #ffffff;
        border-style: solid;
    }

    

    .shield_image {
        position: absolute;
        z-index: 10000;
        top: 42%;
        left: 25%;
        width: 50%;
        animation: shieldFadeIn 7s;
        opacity: 0;
    }



    @keyframes shieldFadeIn {
        0% {
            opacity: 0;
            position: absolute;
        }

        10% {
            opacity: 100%;

        }

        70% {
            opacity: 100%;
            top: 42%;
            left: 25%;
            width: 50%;
            rotate: 0deg;
            width: 50%;
        }

        100% {
            position: absolute;
            opacity: 0;
            top: 150%;
            left: -25%;
            rotate: -90deg;
            width: 1%;

        }
    }

    .shield_title {
        position: absolute;
        top: 30%;
        left: 4%;
        z-index: 100000;
        font-size: 8vw;
        text-align: center;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        background: rgb(0, 0, 0);
        border-radius: 10px;
        animation: shieldTextFadeIn 7s;
        opacity: 0;
    }

    .shield_description {
        position: absolute;
        text-align: center;
        top: 70%;
        left: 1%;
        z-index: 100000;
        font-size: 5vw;
        filter: drop-shadow(0px 0px 25px #000000);
        color: white;
        padding: 2%;
        background: rgb(0, 0, 0);
        border-radius: 10px;
        animation: shieldTextFadeIn 7s;
        opacity: 0;
    }

    @keyframes shieldTextFadeIn {
        0% {
            opacity: 0;
        }

        20% {
            opacity: 100%;

        }

        70% {

            opacity: 100%;
        }

        80% {
            opacity: 0;

        }
    }

    .chest_container {
        width: 100%;
        margin-top: 50%;
        margin-bottom: -60%;
        display: grid;
        grid-template-columns: auto auto auto;
        grid-template-rows: 50% 50%;
    }

    .chest_individual_container {}

    .chest_individual_container_not_picked {
        opacity: 0.6;
        pointer-events: none;
    }

    .chest_grid_item {}

    .chest_title_container {
        position: absolute;
        left: 0;
        right: 0;

    }

    .chest_title {
        margin-top: -30%;
        z-index: 100000;
        font-size: 7vw;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        justify-content: center;
        align-items: center;
        display: flex;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    }

    .chest_title_gone {
        display: none;
    }

    .chest_bottom_image {
        position: absolute;
        width: 30%;
    }

    .chest_top_image {
        position: absolute;
        width: 30%;
        margin-top: 1%;
        z-index: 10;
    }

    .chest_top_image_moved {
        position: absolute;
        margin-top: 1%;
        width: 30%;
        transform: translateY(-80px);
        transition: 1s;
        z-index: 10;
    }

    .forfeit_container {
        position: absolute;
        width: 25%;
        z-index: 100;
        margin-left: 2%;
    }

    .random_forfeit_text {
        justify-content: center;
        align-items: center;
        display: flex;
        margin-top: 20%;
        font-size: 2.5vw;
        font-weight: 10000;
        filter: drop-shadow(0px 0px 20px #727272);
        color: rgb(255, 255, 255);
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        background: rgb(0, 0, 0, 0.7);
        border-radius: 10px;
    }

}

