    .hamburger_icon:hover {
        background-color: #e8e8e8;
    }

    .hamburger_icon:active {
        box-shadow: #000000 2px 2px 0 0;
        transform: translate(2px, 2px);
    }


    .bar1,
    .bar2,
    .bar3 {
        width: 3vw;
        height: 0.4vw;
        background-color: #000000;
        margin: 18%;
        transition: 0.4s;
        border-radius: 8vw;

    }

    .bar1change {
        width: 3vw;
        height: 0.4vw;
        background-color: #000000;
        margin: 18%;
        transition: 0.4s;
        transform: translate(0, 230%) rotate(-45deg);
        border-radius: 8vw;
    }

    .bar2change {
        width: 3vw;
        height: 0.4vw;
        background-color: #000000;
        margin: 18%;
        transition: 0.4s;
        opacity: 0;
    }

    .bar3change {
        width: 3vw;
        height: 0.4vw;
        background-color: #000000;
        margin: 18%;
        transition: 0.4s;
        transform: translate(0, -230%) rotate(45deg);
        border-radius: 8vw;

    }



    .shield_image {
        position: absolute;
        z-index: 10000;
        top: 15%;
        left: 30%;
        width: 40%;
        animation: shieldFadeIn 7s;
        opacity: 0;
    }

    @keyframes shieldFadeIn {
        0% {
            opacity: 0;
            position: absolute;
        }

        10% {
            opacity: 100%;

        }

        70% {
            opacity: 100%;
            top: 15%;
            left: 30%;
            rotate: 0deg;
            width: 40%;
        }

        100% {
            position: absolute;
            opacity: 0;
            top: 150%;
            left: -25%;
            rotate: -90deg;
            width: 1%;

        }
    }

    .shield_title {
        position: absolute;
        top: -8%;
        left: 12%;
        z-index: 100000;
        font-size: 5vw;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        animation: shieldTextFadeIn 7s;
        opacity: 0;
    }

    .shield_description {
        position: absolute;
        top: 80%;
        left: 15%;
        z-index: 100000;
        font-size: 2.5vw;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        animation: shieldTextFadeIn 7s;
        opacity: 0;

    }

    @keyframes shieldTextFadeIn {
        0% {
            opacity: 0;
        }

        20% {
            opacity: 100%;

        }

        70% {

            opacity: 100%;
        }

        80% {
            opacity: 0;

        }
    }

    .chest_container {
        width: 75%;
        margin-left: 5%;
        margin-top: 12%;
        display: grid;
        grid-template-columns: auto auto auto;
        grid-template-rows: 50% 50%;
    }

    .chest_individual_container {}

    .chest_individual_container_not_picked {
        opacity: 0.6;
        pointer-events: none;
    }

    .chest_grid_item {}

    .chest_title_container {
        position: absolute;
        width: 70%;

    }

    .chest_title {
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        margin-top: -15%;
        z-index: 100000;
        font-size: 4vw;
        width: 100%;
        filter: drop-shadow(0px 0px 15px #292929);
        color: white;
        padding: 2%;
        justify-content: center;
        align-items: center;
        display: flex;
    }

    .chest_title_gone {
        display: none;
    }

    .chest_bottom_image {
        position: absolute;
        width: 20%;
    }

    .chest_top_image {
        position: absolute;
        width: 20%;
        margin-top: 1%;
        z-index: 10;
        cursor: pointer;
    }

    .chest_top_image_moved {
        position: absolute;
        margin-top: 1%;
        width: 20%;
        transform: translateY(-200px);
        transition: 1s;
        z-index: 10;
        pointer-events: none;
    }

    .forfeit_container {
        position: absolute;
        width: 22%;
        z-index: 100;
        margin-left: -1%;
        pointer-events: none;
    }

    .random_forfeit_text {
        justify-content: center;
        align-items: center;
        display: flex;
        margin-top: 20%;
        font-size: 2vw;
        font-weight: 10000;
